.navbar-container {
  height: 70px;
  max-width: 1000px;

  padding: 0 20px;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: right;

  background-color: #755181;
}
.navbar-signOut {
  font-size: 25px;
  color: white;

  background-color: black;
  border-radius: 10px;

  padding: 10px 20px;
}
.navbar-container nav{
  margin-top: 10px;

  display: flex;
  justify-content: right;
}
